body {
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
}

@font-face {
  font-family: "JosefinSans-Bold";
  src: local("JosefinSans-Bold"),
   url("./assets/fonts/Josefin_Sans/JosefinSans-Bold.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "JosefinSans-Regular";
  src: local("JosefinSans-Regular"),
   url("./assets/fonts/Josefin_Sans/JosefinSans-Regular.ttf") format("truetype");
  font-weight: normal;
}