* {
  box-sizing: border-box;
}

html {
  height: 100%;
  min-height: 100vh;
}

.light_bg {
  height: 100%;
  min-height: 100vh;
  background-color: hsl(0, 0%, 98%);
  background-image: url('assets/images/bg-desktop-light.jpg');
  background-repeat: no-repeat;
  background-position: center top;
  font-size: 18px;
  font-family: 'JosefinSans-Regular', sans-serif;
}

.dark_bg {
  position: relative;
  background-color: hsl(235, 21%, 11%);
  background-image: url('assets/images/bg-desktop-dark.jpg');
}

#app_container {
  width: 550px;
  margin: 0 auto;
  padding: 80px 0px;
  position: relative;
}

h1 {
  margin-top: 0;
  float: left;
  color: white;
  letter-spacing: 0.3em;
}

.switch_to_dark {
  background-image: url('./assets/images/icon-moon.svg');
  height: 26px;
  width: 26px;
  float: right;
  cursor: pointer;
}

.switch_to_light {
  background-image: url('./assets/images/icon-sun.svg');
}

form {
  width: 100%;
  margin: 35px auto 0px;
  position: relative;
}

.light_input {
  width: 100%;
  height: 50px;
  border: 1px solid hsl(236, 33%, 92%);
  border-radius: 5px;
  font-weight: 500;
  padding-left: 60px;
  font-family: 'JosefinSans-Regular', sans-serif;
  font-size: 18px;
  color: hsl(235, 19%, 35%);
}

.dark_input {
  background-color: hsl(235, 24%, 19%);
  color: hsl(234, 39%, 85%);
  border-color: hsl(237, 14%, 26%);
}

::placeholder {
  font-family: 'JosefinSans-Regular', sans-serif;
  color: hsl(235, 19%, 35%);
  font-size: 18px;
}

.dark_input::placeholder {
  color: hsl(234, 11%, 52%);
}

.light_input_circle {
  width: 22px;
  height: 22px;
  border: 2px solid hsl(236, 33%, 92%);
  border-radius: 50%;
  position: absolute;
  bottom: 15px;
  left: 19px;
}

.dark_input_circle {
  border-color: hsl(237, 14%, 26%);
}

.light_footer {
  width: 100%;
  height: 45px;
  background-color: white;
  border-top: 0.5px solid hsl(236, 33%, 92%);
  border-radius: 0 0 5px 5px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 15px;
  font-weight: bold;
  color: hsl(236, 9%, 61%);
}

.dark_footer {
  background-color: hsl(235, 24%, 19%);
  border-color: hsl(237, 14%, 26%);
  color: hsl(233, 14%, 45%);
}

.light_counter {
  float: left;
  color: hsl(236, 9%, 70%);
}

.dark_counter {
  color: hsl(233, 14%, 35%);
}

nav {
  width: 245px;
  margin-left: 90px;
}

ul {
  list-style-type: none;
}

ul li {
  float: left;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}

ul li:hover {
  color: hsl(235, 19%, 35%);
}

.dark_footer ul li:hover {
  color: hsl(234, 39%, 85%)
}

.selected_filter {
  color: hsl(220, 98%, 61%);
}

.light_clear {
  float: right;
  padding: 0;
  border: none;
  background: none;
  color: hsl(236, 9%, 70%);
  font-size: 15px;
  font-family: 'JosefinSans-Regular', sans-serif;
  font-weight: bold;
  cursor: pointer;
}

.dark_clear {
  color: hsl(233, 14%, 35%);
}

.light_clear:hover {
  color: hsl(235, 19%, 35%);
}

.dark_clear:hover {
  color: hsl(234, 39%, 85%)
}

.light_drag {
  color: hsl(236, 9%, 70%);
  font-size: 15px;
  font-weight: bold;
  width: 245px;
  margin: 60px auto;
}

.dark_drag {
  color: hsl(233, 14%, 35%);
}