#items_container {
    width: 550px;
    height: auto;
    margin-top: 25px;
    border-radius: 5px 5px 0 0;
    overflow: hidden;
}

.list {
  width: 100%;
  height: 50px;
  border-top: 0.5px solid hsl(236, 33%, 92%);
  border-bottom: 0.5px solid hsl(236, 33%, 92%);
  color: hsl(235, 19%, 35%);
  background-color: white;
  padding-left: 60px;
  background-clip: padding-box;
  position: relative;
  cursor: pointer;
}

.dark_list {
  background-color: hsl(235, 24%, 19%);
  color: hsl(234, 39%, 85%);
  border-color: hsl(237, 14%, 26%);
}

.untick_circle {  
  width: 22px;
  height: 22px;
  border: 2px solid hsl(236, 33%, 92%);
  border-radius: 50%;
  background-color: white;
  position: absolute;
  top: -1px;
  left: -1px;
}

.dark_untick_circle {
  background-color: hsl(235, 24%, 19%);
  border-color: hsl(237, 14%, 26%);
}

.tick_circle {
  display: none;
}

.crossover_text {
  color: hsl(236, 9%, 83%);
  text-decoration: line-through;
}

.dark_crossover_text {
  color: hsl(233, 14%, 35%);
  text-decoration: line-through;
}

.circle_border_wrap {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: 15px;
  left: 20px;
  background: url('./assets/images/icon-check.svg'), linear-gradient(to bottom right, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
  background-repeat: no-repeat;
  background-position: center;
}

.item_circle:hover {
  width: 17px;
  height: 17px;
  position: absolute;
  top: 1.5px;
  left: 1.5px;
  border: 2px solid transparent;
}

.item_cross {
  width: 13px;
  height: 13px;
  background-image: url('./assets/images/icon-cross.svg');
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 19px;
  right: 20px;
  display: none;
}

.list:hover > .item_cross {
  display: block;
}